:root {
  --primery-text-color: #757575;
  --second-text-color: #2196f3;
  --third-text-color: #fff;
  --fourth-text-color: #212121;
  --accent-color: #2196f3;
  --second-accent-color: #fff;
  --primary-bg-color: #2196f3;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

input {
  outline: none;
}

a {
  color: inherit;
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

address {
  font-style: normal;
}

.page-header {
  width: 100%;
  height: 100px;
  padding-top: 38px;
  padding-bottom: 38px;
  position: relative;
}

.page-header__logo-link {
  position: absolute;
  top: 50%;
  left: 145px;
  transform: translateY(-50%);
}

.page-header__logo {
  fill: #333;
}

.page-header__nav-list {
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  display: flex;
}

.page-header__nav-item {
  text-transform: uppercase;
  font-family: roboto;
  font-size: 14px;
  font-weight: 300;
  line-height: 1.71;
}

.page-header__nav-item:not(:last-child) {
  margin-right: 35px;
}

.page-header__nav-link {
  color: #000;
  padding-top: 15px;
  padding-bottom: 15px;
  text-decoration: none;
}

.page-header__button {
  width: 200px;
  height: 60px;
  color: #fff;
  font-family: roboto;
  font-style: 300;
  letter-spacing: .1em;
  text-transform: uppercase;
  background-color: #5b6460;
  border: none;
  margin-left: auto;
  font-size: 14px;
  line-height: 2;
  position: absolute;
  top: 50%;
  right: 30px;
  transform: translateY(-50%);
}

.page-header__button:hover, .page-header__button:focus {
  cursor: pointer;
}

.footer {
  width: 100%;
  height: 475px;
  background-color: #5b6460;
  padding: 100px 145px;
  display: flex;
}

.footer__letter-title {
  color: #efefef;
  margin-bottom: 40px;
  font-family: Open Sans;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.88;
}

.footer__letter-form {
  width: 430px;
  border-bottom: 1px solid #fff;
  margin-bottom: 10px;
  position: relative;
}

.footer__letter-form-input {
  width: 100%;
  color: #fff;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  font-family: Open Sans;
  font-size: 16px;
  font-style: italic;
  font-weight: 400;
  line-height: 1.88;
}

.footer__letter-button {
  color: #fff;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}

.footer__letter-text {
  max-width: 420px;
  color: #fff;
  font-family: Open Sans;
  font-size: 16px;
  font-weight: 300;
  line-height: 1.88;
}

.footer__letter-symbol {
  color: #efefef;
  margin-top: auto;
  font-family: Open Sans;
  font-size: 14px;
  font-weight: 300;
  line-height: 2.13;
}

.footer__box {
  background-color: rgba(0, 0, 0, 0);
}

.footer__box-list {
  display: flex;
}

.footer__box-item {
  background-color: rgba(0, 0, 0, 0);
}

.footer__box-item:not(:last-child) {
  margin-right: 30px;
}

.footer__box-link {
  background-color: rgba(0, 0, 0, 0);
}

